import { createRouter, createWebHistory } from 'vue-router'
import HomeView from '../views/HomeView.vue'
import about from '../views/AboutView.vue'
import blog from '../views/BlogView.vue'
import user from '../views/UserView.vue'
import BlogToView from '../views/blog/BlogToView.vue'
import vuea from '../views/blog/VueInstallationa.vue'
import vueb from '../views/blog/VueInstallationb.vue'
import javaweba from '../views/blog/JavaWeba.vue'
const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/about', // 超链接to的地址
    name: 'about', // 路由的名字可有可无
    component: about
  },
  {
    path: '/blog',
    name: 'blog',
    component: blog
  },
  {
    path: '/user',
    name: 'user',
    component: user
  },
  {
    path: '/BlogToView',
    name: 'BlogToView',
    component: BlogToView
  },
  {
    path: '/vuea',
    component: vuea
  },
  {
    path: '/vueb',
    component: vueb
  },
  {
    path: '/javaweba',
    component: javaweba
  }
]

const router = createRouter({
  base: process.env.BASE_URL,
  history: createWebHistory(),
  routes
})

export default router
